export interface IContent {
    number: number;
    type: 'img' | 'text';
    imgFileName?: string;
    imgBase64?: string;
    alt?: string;
    width?: number;
    height?: number;
    text?: string;
}

export interface IEditContent {
    number: number;
    type: 'img' | 'text';
    imgFileName?: string;
    imgBase64?: string;
    altZh?: string;
    altEn?: string;
    width?: number;
    height?: number;
    textZh?: string;
    textEn?: string;
}

export const initialContent:IContent = {
    number: -1,
    type: 'text'
}

export interface IArticle {
    title: string;
    content: IContent[];
}

export const initialArticle:IArticle = {
    title: '',
    content: []
}

export interface INewsItem {
    uuid: number;
    date: string;
    en: IArticle | null;
    zh: IArticle | null;
    isNew: boolean;
}

export const initialNews:INewsItem = {
    uuid: -1,
    date: '',
    en: null,
    zh: null,
    isNew: true
}

